.home-bg {
    background: linear-gradient(to bottom right, #002171, #0d47a1);
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    font-family: 'Segoe UI', sans-serif;
  }
  
  .home-card {
    background: rgba(255, 255, 255, 0.12);
    backdrop-filter: blur(10px);
    border-radius: 1rem;
    padding: 2rem 3rem;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
    color: white;
    text-align: center;
    max-width: 420px;
  }
  
  .home-card h1 {
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
  
  .home-card p {
    margin-bottom: 2rem;
    font-size: 1rem;
    color: #e0e0e0;
  }
  
  .home-buttons {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .home-buttons button {
    background-color: #ffffff;
    color: #0d47a1;
    border: none;
    padding: 0.75rem 1.5rem;
    border-radius: 0.5rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .home-buttons button:hover {
    background-color: #e3f2fd;
  }
  
  .logout-button {
    margin-top: 1.5rem;
    background-color: transparent;
    border: 2px solid #ffffff;
    color: white;
    padding: 0.6rem 1.5rem;
    border-radius: 0.5rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .logout-button:hover {
    background-color: #ffffff;
    color: #0d47a1;
  }
  